	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"You Never Forget","date":"02 Jan 2006","slug":"/2006-01-02-you-never-forget/","tags":"Y2006"},"html":"<p>Jeremy Zawodny remembers <a href=\"http://jeremy.zawodny.com/blog/archives/006016.html\" title=\"You Never Forget Your First Web Server (by Jeremy Zawodny)\">his first web server</a> 10 years ago. He sure had it good. Static IP address? Direct Internet connection? My first memories of running an Internet server was a mail server running UUCP over a 36K analog modem. My firm at the time was just over 50 km away from Hamburg, our access point, so we were paying expensive Deutsche Telekom long distance rates to get our mail and update our website. Good thing spam hadn't been invented yet. That was at least a step up from the CompuServe address we had been using until then. Oh yes, those were the days.</p>\n<p>The first web server I had that I could put my hands on was at my next company at the turn of the century and the peak of the bubble. That 400 MHz Pentium was among inventory sold to employees as the company headed to bankruptcy, so I bought it and it's now the firewall and router in my basement.  </p>"},"next":{"fields":{"title":"Wordpress 2","date":"01 Jan 2006","slug":"/2006-01-01-wordpress-2/"}},"previous":{"fields":{"title":"Could You Pass the Test?","date":"04 Jan 2006","slug":"/2006-01-04-could-you-pass-the-test/"}}},{"node":{"fields":{"title":"Wordpress 2","date":"01 Jan 2006","slug":"/2006-01-01-wordpress-2/","tags":"Y2006"},"html":"<p>Yesterday I updated PapaScott to<a href=\"http://wordpress.org/development/2005/12/wp2/\"> Wordpress 2</a>. I haven't looked all that closely at it, but all the plugins I use still work and posting with MarsEdit is quite a bit faster. However, my two main pet peeves with Wordpress remain. First, there is no export function (so if you want to move your Wordpress blog to another system, prepare to learn a lot of SQL). Second, if you want to move a site to a new URL (if you want to test a new version, for example) you have to edit the database manually (otherwise the admin pages all redirect to the old URL). </p>"},"next":{"fields":{"title":"Snow Angel","date":"01 Jan 2006","slug":"/2006-01-01-snow-angel/"}},"previous":{"fields":{"title":"You Never Forget","date":"02 Jan 2006","slug":"/2006-01-02-you-never-forget/"}}},{"node":{"fields":{"title":"Snow Angel","date":"01 Jan 2006","slug":"/2006-01-01-snow-angel/","tags":"Y2006"},"html":"<p><a href=\"http://www.flickr.com/photos/papascott/80140541/\" title=\"Photo Sharing\"><img src=\"http://static.flickr.com/41/80140541_8c83424fb6.jpg\" width=\"500\" height=\"378\" alt=\"IM000418\" /></a></p>"},"next":{"fields":{"title":"New Year","date":"01 Jan 2006","slug":"/2006-01-01-new-year/"}},"previous":{"fields":{"title":"Wordpress 2","date":"01 Jan 2006","slug":"/2006-01-01-wordpress-2/"}}}],"pathPrefix":"","first":false,"last":false,"index":252,"pageCount":1101}}

